body {
  font-family: Arial, Helvetica, sans-serif; }

#background-left {
  position: absolute;
  left: 0;
  z-index: -1; }

#background-right {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: -1; }

h1 {
  text-align: center; }

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; }
  .wrapper .todolist input {
    text-align: center;
    width: 836px;
    border-radius: 15px;
    height: 60px;
    font-size: 30px;
    margin-bottom: 2%;
    border: none;
    box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.25); }
  .wrapper .todolist .list {
    padding-top: 5%;
    background: #ffffff;
    width: 836px;
    height: 45vh;
    box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.25);
    border-radius: 15px;
    justify-content: center;
    align-items: center;
    overflow: scroll; }
    .wrapper .todolist .list .outer-item {
      animation-name: fadeOut;
      margin-top: 2%;
      display: flex;
      align-items: center;
      justify-content: center; }
      .wrapper .todolist .list .outer-item .to-do-item {
        padding-left: 2%;
        display: flex;
        align-items: center;
        background: #cac5dc;
        box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.25);
        border-radius: 10px;
        height: 60px;
        width: 95%;
        justify-content: space-between; }
        .wrapper .todolist .list .outer-item .to-do-item span {
          text-align: right;
          cursor: pointer;
          margin-right: 2%; }
